nav{
    background-color:#29b6f6!important;
}
    

.container{
    padding-top:50px;
} 

.card:hover {
    box-shadow: 5px 10px 20px 1px rgba(121, 89, 89, 0.74) !important;
    transition: all 0.2s linear;
    -webkit-transform: scale(1.01);
            transform: scale(1.01);
    cursor: pointer;
}
 
  
.card .card-image img {
    padding: 20px;
}


.card .card-content .card-title {
    font-size:15px;
    font-weight:600;
    color: black;
    line-height: 17px!important
}

.card-content p {
    display: flex;
    align-self: flex-end;
    font-size: 15px;
    font-weight:bold;
}

.product{
    padding-left:20px;
}

.product-price strong{
    margin-left: 25px;
    font-size: 20px;
}


@media(max-width: 400px){ 
    .card.horizontal{
        flex-direction: column;
        align-items: baseline;

    }
}

.btn-flat{
    background-color: #1c6d5a82;
    padding: 0 26px;
    margin: 6px;
    color: black
}

.dark {
    background-color: #333;
    color: #fff;
  }
  .light {
    background-color:#e8edf0;
    color: #333;
  }

  .dark .body-login {
    background: #041612!important;
    background: linear-gradient(90deg, rgba(51, 71, 67, 0.942) 0%, rgba(40, 46, 107, 0.992) 61%, rgb(56, 31, 38) 100%)!important ;
    padding-left: 20px;
    width: 100vw;
    min-height: 100vh;
}
.dark nav{
    background-color:#1f2e35!important
}
.dark .card .medium{
    background-color: rgb(28 54 60 / 73%)!important;
}

.dark .card.horizontal{
    background-color:#23242188!important;
}
.body-login {
    background: rgb(10,142,112);
    background: linear-gradient(90deg, rgba(10,142,112,0.9416141456582633) 0%, rgba(25,40,224,0.9920343137254902) 61%, rgba(153,130,136,1) 100%);
    padding-left:20px;
    /* width: 100vw; */
    min-height: 100vh;
}
.loggin_page {
    padding:25px;
    margin: 30px;
}
.title-login {
    color: aliceblue;
    margin:1px;
    padding-top:20px;
    margin-left: 50px;
}

.input-field input {
    color: rgba(248, 248, 248, 0.781);
}
#email,#nuevoEmail{ 
    text-transform: lowercase;
}

 ul li .buttons{
    border-radius: 15px;
    background: #649a90;
    background: linear-gradient(24deg, #649a90 0%, #7848e0 100%);
}
